.backdrop {
  width: 100%;
  height: 100%;
  background-color: var(--modal-background);
  z-index: 99;
  opacity: 1;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
  overflow: scroll;
}

.backdrop .backdrop.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  transform: scale(.5);
}

.backdrop.is-hidden .modal {
  transform: scale(.5);
}

