.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: var(--modal-background);
  z-index: 99;
  opacity: 1;
  overflow: scroll;

  transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1),
    visibility 250ms cubic-bezier(0.4, 0, 0.2, 1);

  .backdrop.is-hidden {
    opacity: 0;
    pointer-events: none;
    visibility: hidden;
    transform: scale(0.5);

    transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1),
      visibility 250ms cubic-bezier(0.4, 0, 0.2, 1);
  }
}

.backdrop.is-hidden .modal {
  transform: scale(0.5);
}

.modal {
  width: 100%;
  max-width: 280px;
  max-height: 600px;
  justify-content: center;

  overflow-y: auto;

  // overflow: auto;
  background: #ffffff;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(1);
  box-shadow: 0px 1px 3px rgba (0 0 0, 0.12), 0px 1px 1px rgba (0 0 0, 0.14),
    0px 2px 1px rgba (0 0 0, 0, 2);
  transition: 250ms;
  padding-top: 40px;
  padding-left: 20px;
  padding-right: 20px;
  padding-bottom: 40px;

  @media (min-width: 768px) {
    max-width: 704px;
    padding-left: 36px;
    padding-right: 72px;
    max-height: 100%;
  }

  @media (min-width: 1280px) {
    max-width: 806px;
    padding-left: 12px;
    padding-right: 12px;
  }
}

.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

.modal-container {
  display: flex;
  flex-direction: column;

  @media (min-width: 768px) {
    flex-direction: row;
    width: 100%;
  }
}

.modal-poster {
  min-width: 240px;

  @media (min-width: 768px) {
    margin-bottom: 0px;
    margin-right: 68px;
    min-width: 264px;
  }

  @media (min-width: 1280px) {
    margin-right: 16px;
    min-width: 375px;
  }
}

.poster {
  border-radius: 5px;
  display: block;
  margin-bottom: 20px;
}

.film-info__about {
  margin-bottom: 20px;
}

.modal__title {
  text-transform: uppercase;

  font-size: 20px;
  font-weight: 500;
  line-height: 1.17;

  margin-bottom: 20px;
}

.modal-table {
  margin-bottom: 20px;
}

.modal-table__row + .modal-table__row {
  margin-bottom: 8px;
}

.modal-table__title {
  width: 67px;
  padding-right: 39px;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.33;
  color: var(--modalCardCharacterFilm);

  @media (min-width: 1280px) {
    padding-right: 83px;
  }
}

.modal-table__info {
  font-weight: 500;
  font-size: 12px;
  line-height: 1.33;
}

.uppercase {
  text-transform: uppercase;
}

.rate {
  background: #ff6b01;
  border-radius: 5px;
  padding-top: 1px;
  padding-bottom: 1px;
  padding-left: 10px;
  padding-right: 10px;
  color: var(--white);
}

.vote-count {
  background: var(--footerColor);
  border-radius: 5px;
  padding-top: 1px;
  padding-bottom: 1px;
  padding-left: 10px;
  padding-right: 10px;
  color: var(--darkColor);
}

.backslash {
  font-weight: 500;
  font-size: 12px;
  line-height: 1.33;
  color: var(--modalCardCharacterFilm);
}

.row {
  display: flex;
  flex-direction: row;
}

.modal-about {
  font-weight: 500;
  font-size: 12px;
  line-height: 1.33;
  text-transform: uppercase;

  margin-bottom: 8px;
}

.modal-about__text {
  text-align: left;
  font-weight: 500;
  line-height: 1.66;

  @media (min-width: 768px) {
    height: 100%;
  }
}

.buttons {
  display: flex;
  justify-content: space-between;

  @media (min-width: 768px) {
    justify-content: flex-start;
  }
}

.addInStorageWantWatch {
  width: 110px;
  height: 44px;

  text-transform: uppercase;

  font-weight: 500;
  font-size: 12px;
  line-height: 1.33;
  border-radius: 5px;
  border: 1px solid var(--darkColor);
  background-color: var(--white);
  transition: all 250ms linear;
  &.action-library {
    background-color: var(--activeColor);
  }
  @media (min-width: 768px) {
    margin-right: 15px;
    width: 125px;
  }

  @media (min-width: 1280px) {
    width: 136px;
  }

  &:hover,
  &:focus {
    border: none;
    background-color: var(--buttonActive);
    color: var(--white);
    transition: all 250ms linear;
  }
}

.addInStorageAlreadyWatched {
  width: 110px;
  height: 44px;
  text-transform: uppercase;
  background-color: var(--white);

  font-weight: 500;
  font-size: 12px;
  line-height: 1.33;
  border-radius: 5px;
  border: 1px solid var(--darkColor);
  transition: all 250ms linear;
  &.action-library {
    background-color: var(--activeColor);
  }
  @media (min-width: 768px) {
    width: 125px;
  }

  @media (min-width: 1280px) {
    width: 136px;
  }

  &:hover,
  &:focus {
    border: none;
    background-color: var(--buttonActive);
    color: var(--white);
    transition: all 250ms linear;
  }
}

.modal__add-to-watched {
  margin-right: 15px;
}

.modal__add-to-watched,
.modal__add-to-queue {
  background: #ffffff;
  border-radius: 5px;
  border: 1px solid #000000;
  color: #000000;
  font-size: 12px;
  padding: 8px;
}

.modal__add-to-watched:hover,
.modal__add-to-queue:hover {
  background-color: var(--activeColor);
  color: var(--white);
  transition: background-color 0.4s;
}

.modal__close {
  width: 30px;
  height: 30px;
  border: none;
  background-color: #ffffff;
  position: absolute;
  top: 8px;
  right: 8px;
}

.modal__close-icon {
  width: 12px;
  height: 12px;
}
